}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Inter", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #2C2C2C;
  font-size: 1.5rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "TitlingGothicFBComp-Regular", sans-serif;
  margin: 2.5rem 0;
  font-weight: 700;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2rem;
  font-weight: 700;
}

.img-bgAzul {
  background-color: rgb(52, 114, 173);
}

.img-bgNaranja {
  background-color: #f08233;
}

.bg-naranja {
  background-color: #e54c0f;
  color: #ffffff;
}

.bg-azul {
  background-color: #193758;
  color: #ffffff;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.btn-naranja {
  background-color: #e54c0f;
  color: #ffffff;
  border-style: none;
  border-radius: 16px;
  width: 100%;
  height: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #193758;
}
.navbar .navbar-logo {
  width: 10rem;
  height: 10rem;
}
.navbar a {
  margin: 10px;
  color: #ffffff;
}
.navbar a:hover {
  color: #f08233;
}
.navbar .redesSociales {
  display: flex;
  flex-direction: row;
}
.navbar .logo-redes {
  width: 24px;
  height: auto;
}
@media (min-width: 768px) {
  .navbar {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
  }
}

.contenedor-propuestas-principales {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.contenedor-propuestas-principales .propuestas-principales {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  margin: 10px;
  border-radius: 16px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px 1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}
.contenedor-propuestas-principales .propuestas-principales a {
  color: #e54c0f;
}
@media (min-width: 768px) {
  .contenedor-propuestas-principales {
    flex-direction: row;
    justify-content: center;
  }
  .contenedor-propuestas-principales .propuestas-principales {
    margin: 64px 10px;
    width: 33.33%;
    height: 300px;
  }
}

.contenedor-inicio-conoceme {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: justify;
  padding: 64px 0;
}
.contenedor-inicio-conoceme .conoceme-inicio-texto {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: justify;
  margin: 24px;
}
.contenedor-inicio-conoceme .conoceme-inicio-texto h2 {
  margin: 24px 0;
}
.contenedor-inicio-conoceme .conoceme-inicio-texto p {
  margin: 24px 0;
}
.contenedor-inicio-conoceme .conoceme-inicio-texto a {
  margin: 24px 0;
}
@media (min-width: 768px) {
  .contenedor-inicio-conoceme .conoceme-inicio-texto {
    width: 40%;
  }
}
@media (min-width: 768px) {
  .contenedor-inicio-conoceme {
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    align-items: center;
  }
  .contenedor-inicio-conoceme .btn-naranja {
    width: 168px;
    height: 48px;
  }
}
.contenedor-inicio-conoceme .conoceme-inicio-video {
  display: flex;
  justify-content: center;
}
.contenedor-inicio-conoceme .conoceme-inicio-video video {
  width: auto;
  height: 600px;
  border-radius: 16px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}

.btn-naranja {
  width: 100%;
  height: 48px;
}

.conoceme-video video {
  width: 300px;
  height: 300px;
}
@media (min-width: 768px) {
  .conoceme-video {
    width: 60%;
    height: 600px;
  }
}

.contenedor-ejesYPrincipios .ejesYPrincipios {
  padding: 2rem;
  border-radius: 16px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}
.contenedor-ejesYPrincipios .ejesYPrincipios img {
  width: 64px;
  height: auto;
}
.contenedor-ejesYPrincipios .ejesYPrincipios a {
  color: #e54c0f;
}
@media (min-width: 768px) {
  .contenedor-ejesYPrincipios {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
    height: auto;
    margin-bottom: 2rem;
  }
}

.contenedor-ejes .contenedor-ejes .ejes,
.contenedor-ejes .contenedor-ejes .principios,
.contenedor-principios .contenedor-ejes .ejes,
.contenedor-principios .contenedor-ejes .principios {
  padding: 2rem;
  margin: 10px 0;
  border-radius: 16px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}
@media (min-width: 768px) {
  .contenedor-ejes .contenedor-ejes,
  .contenedor-principios .contenedor-ejes {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
    height: auto;
  }
  .contenedor-ejes .contenedor-ejes .vidaYFamilia,
  .contenedor-ejes .contenedor-ejes .PuertasAbiertas,
  .contenedor-principios .contenedor-ejes .vidaYFamilia,
  .contenedor-principios .contenedor-ejes .PuertasAbiertas {
    grid-column: 1/3;
  }
}
@media (min-width: 1024px) {
  .contenedor-ejes .contenedor-ejes,
  .contenedor-principios .contenedor-ejes {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
    height: auto;
  }
  .contenedor-ejes .contenedor-ejes .vidaYFamilia,
  .contenedor-ejes .contenedor-ejes .PuertasAbiertas,
  .contenedor-principios .contenedor-ejes .vidaYFamilia,
  .contenedor-principios .contenedor-ejes .PuertasAbiertas {
    grid-column: 2/4;
  }
}

.contenedor-propuestas .propuestas {
  display: flex;
  flex-direction: row;
  padding: 20px;
  margin: 20px;
  border-radius: 16px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}
.contenedor-propuestas .propuestas h4,
.contenedor-propuestas .propuestas p {
  margin: 0;
}
.contenedor-propuestas .propuestas img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin: 10px;
}

.contenedor-conoceme {
  margin-top: 64px;
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  padding: 32px;
  border-radius: 16px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}
.contenedor-conoceme h2 {
  letter-spacing: 1px;
  padding: 0 24px;
  color: #ffffff;
  border-radius: 8px;
}