.contenedor-ejesYPrincipios{
    .ejesYPrincipios{
        padding: 2rem;
        border-radius: 16px;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;

        img {
            width: 64px;
            height: auto;
        }

        a{
            color: $naranjaFuerte
        }
    }
    
    @include tablet {
        @include grid(2,1rem);
        margin-bottom: 2rem;
    }
}